
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import OFooter from "@/components/common/OFooter";
export default {
components: {
OFooter,
Badge,
Group,
Cell,
ViewBox,
AlertModule
},
data() {
return {
list: []
};
},
methods: {
getAuditTaskMenu() {
let params = {
params: {
RuleName: "GetAuditTaskMenu",
Parm: { TimePoint: formatDate(new Date(), 'yyyyMMdd') }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.list = this.list.concat(data.RspMsg.result.ListItems.ListItem || []);
}
else {
this.showModule("获取数据失败", data.RspMsg.msg);
}
this.$store.commit('updateLoadingStatus', { isLoading: false })
};
callPuExctQryToOICS(params, callBackSuccess);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
},//是否启用
isDisabled(menuId) {
let isDisabled = false;
if (menuId == '1406' || menuId == '1409') isDisabled = false
else isDisabled = true
return isDisabled;
},
getLink(menuId) {
let typeValue = "";
let typeName = "SQSP";
let titleName = "";
let ruleName = "";
let linkName = "";

if (menuId == '1406') {
typeValue = "6";
titleName = "录入核查反馈意见";
ruleName = "GetFeedbackSuperviseList";
linkName = "subsequent_supervision/information_entry";
}
else if (menuId == '1409') {
typeValue = "7";
titleName = "提交核查反馈意见";
ruleName = "GetSubmitFeedbackSuperviseList";
linkName = "";
}
return {
path: "/subsequent_supervision/supervision_list", query: { typeValue: typeValue, typeName: typeName, titleName: titleName, ruleName: ruleName, linkName: linkName }
};
},//获取菜单名字
getMenuName(menuId) {
let menuName = "";
if (menuId == '1401') menuName = "总行集中业务事后监督"
else if (menuId == '1402') menuName = "分行集中业务事后监督"
else if (menuId == '1403') menuName = "网点平台业务事后监督"
else if (menuId == '1404') menuName = "COP业务事后监督"
else if (menuId == '1405') menuName = "发出监督核查"
else if (menuId == '1406') menuName = "录入核查反馈意见"
else if (menuId == '1409') menuName = "提交核查反馈意见"
else if (menuId == '1410') menuName = "生成核查结论"
else if (menuId == '1411') menuName = "审核核查结论"
return menuName;
}
},
mounted() {


},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.list = [];
vm.getAuditTaskMenu();
})
}
};



